import {WebAPI} from './web-api';
import {inject, NewInstance} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
import { ValidationController, ValidationRules } from 'aurelia-validation';
import { MaterializeFormValidationRenderer } from 'aurelia-materialize-bridge';
import { MdToastService } from 'aurelia-materialize-bridge';
import {Router} from 'aurelia-router';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI, NewInstance.of(ValidationController), MdToastService, Router)
export class ContactNew {
  rules = ValidationRules
    .ensure('contactName')
      .required()
        .withMessage('連絡先名称は必須です')
    .ensure('orderDisplay')
      .required()
        .withMessage('表示順は必須です')
      .matches(/[0-9]+/)
        .withMessage('数値を指定して下さい')
      .rules;

  constructor(api, controller, toast, router) {
    this.api = api;
    this.controller = controller;
    this.controller.addRenderer(new MaterializeFormValidationRenderer());
    this.toast = toast;
    this.router = router;

    this.contactName = '';
    this.orderDisplay = '';
    this.note = '';
    this.activeFlg = true;
    log.info('ContactNew-constructor');
  }

  validateModel() {
    this.controller.validate().then(v => {
      if (v.valid) {
        log.info('pass-1');
        let body = [{
          'contactName': this.contactName,
          'orderDisplay': this.orderDisplay,
          'activeFlg': this.activeFlg ? 1 : 0,
          'flgChangeDate': null,
          'note': this.note,
          'updateEmployeeCD': 'izu_t'
        }];
        this.api.addContact(body).then(results => {
          log.info('Success');
          log.info(results);
          this.router.navigate('/');
        }).catch(error => {
          log.info('Error!');
          log.info(error);
        });
      } else {
        //this.message = 'You have errors!';
        this.toast.show('You have errors!', 4000, 'red white-text');
      }
    });
  }

  created() {
    log.info('ContactNew-created');
  }
}
