import {LogManager} from 'aurelia-framework';

let log = LogManager.getLogger('web-api');

export class WebAPI {
  isRequesting = false;

  getContactList(page, lineCount) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    let skipRows = (page - 1) * lineCount;
    strUrl += skipRows + '/' + lineCount;
    return fetch(strUrl)
      .then(response => {
        this.isRequesting = false;
        log.info('Success!');
        log.info(response);
        if (response.status !== 200) {
          rejected(response);
        } else {
          return response.json();
        }
      }).catch(error => {
        this.isRequesting = false;
        log.info('Error!');
        log.info(response);
        return error.json();
      });
  }

  addContact(postData) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    return fetch(strUrl, {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(postData)
    }).then(response => {
      this.isRequesting = false;
      log.info('Success!');
      log.info(response);
      if (response.status !== 200) {
        rejected(response);
      } else {
        return response.json();
      }
    }).catch(error => {
      this.isRequesting = false;
      log.info('Error!');
      log.info(response);
      return error.json();
    });
  }

  delContact(postData) {
    this.isRequesting = true;
    let strUrl = 'https://izufr01.azurewebsites.net/api/SA21/01/';
    return fetch(strUrl, {
      method: 'DELETE',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(postData)
    }).then(response => {
      this.isRequesting = false;
      log.info('Success!');
      log.info(response);
      if (response.status !== 200) {
        rejected(response);
      } else {
        return response.json();
      }
    }).catch(error => {
      this.isRequesting = false;
      log.info('Error!');
      log.info(response);
      return error.json();
    });
  }
}
