import {WebAPI} from './web-api';
import {inject} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI)
export class ContactList {
  constructor(api) {
    this.api = api;
    this.contacts = [];
    this.activePage = 1;
    this.maxPage = 1;
    this.lineCount = 10;
    this.onCreating = false;
    this.selectedIdx = -1;
  }

  showList() {
    this.api.getContactList(this.activePage, this.lineCount).then(contacts => {
      log.info('Success!');
      log.info(contacts);
      this.contacts = contacts.map((e) => {
        e.selected = false;
        return e;
      });
      let allLineCount = contacts[0].LineCount;
      this.maxPage = Math.floor(allLineCount / this.lineCount);
      if (allLineCount % this.lineCount !== 0) {
        this.maxPage += 1;
      }
    }).catch(error => {
      log.info('Error!');
      log.info(error);
    });
  }

  created() {
    this.onCreating = true;
    log.info('created!');
    this.showList();
  }

  onPageChanged(e) {
    log.info(`page changed ${e.detail}`);
    if (this.onCreating) {
      this.onCreating = false;
    } else {
      this.activePage = e.detail;
      this.showList();
    }
    if (this.selectedIdx >= 0) {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
    }
  }

  onClickRow(idx) {
    log.info(idx);
    if (this.selectedIdx === idx) {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
    } else {
      if (this.selectedIdx >= 0) {
        this.contacts[this.selectedIdx].selected = false;
      }
      this.contacts[idx].selected = true;
      this.selectedIdx = idx;
    }
  }

  openDelModal() {
    this.delmdl.open();
  }

  onDelAgree() {
    log.info('del-agree');
    let body = [{
      'contactCD': this.contacts[this.selectedIdx].MCNTCT_ContactCD
    }];
    this.api.delContact(body).then(contacts => {
      log.info('Success!');
      log.info(contacts);
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
      this.showList();
    }).catch(error => {
      log.info('Error!');
      log.info(error);
    });
  }
}
